<template>
  <div>
    <input v-model="parentMsg">
    {{parentMsg}}+{{age}}
    <br>
    <child v-bind:message="parentMsg"></child>
    <button v-on:click="childButton"></button>
  </div>
</template>
 
<script>
import Vue from "vue";
// 
Vue.component("child", {
  //  props
  props: ["message"],
  //  vm  "this.message" 
  template: '<input v-model="message">'
});
// 
var _dataModel = { age: "111" };
export default {
  name: "test",
  data() {
    return {
      age: _dataModel.age,
      parentMsg: "111"
    };
  },
  activated: function() {
    //keep-alive 
    console.log("test-activated");
  },
  deactivated: function() {
    //keep-alive 
    console.log("test-deactivated");
  },
    beforeDestroy: function() {
    console.log("test-beforeDestroy");
  },
  destroyed: function() {
    console.log("test-destroyed");
  },
  methods: {
    childButton: function() {
      this.$emit("childButton", this.parentMsg);
    },
    childClick: function(value) {
      alert(value);
    },
  }
};
</script>


