<template>
  <div class="vuex-main-div">
    <h2>vuex</h2>
    Vuex  Vue.js 
    <h3></h3>
    
    <h2>vuex</h2>
    1<br>
    2<br>
    3<br>
    4<br>
    <h2>Vuex</h2>
     State GetterMutation Action Module<br>
    State: stateVuex, statedata, <br>
    Getter: gettervuex<br>
    Mutation: mutaionsstoremethods<br>
    Action:  mutation!!!Action mutations<br>
    Module: storemodule<br>
    <h2>vuex</h2>

    <br/>
    <br/>
    <h3>state{{this.$store.state.count}}</h3>
    <h3>mapState count{{count}}</h3>
    <h3>mapState countFunc{{countFunc}}</h3>
    <br/>

    <h3>getter{{this.$store.getters.getStoreStateCount}}</h3>
    <h3>mapGetters getStoreStateCount{{getStoreStateCount}}</h3>
    <h3>mapGetters getTrueObjList{{getTrueObjList}}</h3>
    <br/>

    <button @click="add_def"></button>
    <button @click="add10"></button>
    <button @click="remove_def"></button>
    <button @click="remove10"></button>
    <br/>
    <button @click="add(1)"></button>
    <button @click="addFunc(10)"></button>
    <button @click="remove(1)"></button>
    <button @click="removeFunc(10)"></button>
    <br/>
    <h3>m1 state{{this.$store.state.m1.num}}</h3>
    <h3>m2 state{{this.$store.state.m2.num}}</h3>
    <h3>m1 state version{{this.$store.state.m1.version}}</h3>
    <h3>m2 state version{{this.$store.state.m2.version}}</h3>
    <h3>m1 ...mapState num {{num1}}</h3>
    
    <br/>
    <button @click="changedM1(1)">m1 num </button>
    <button @click="changedM2(10)">m2 version </button>
    <button @click="setNum1(999)">m1 num </button>
    <button @click="setNumFunc2(200)">m2 version </button>

  </div>
</template>

<script>
import { mapState, mapActions, mapGetters, mapMutations } from "vuex"; //map
export default {
  data() {
    return {
      inputValue: "me",
      myCount: 10,
      index:1
    };
  },
  name: "vuex",
  components: {},
  methods: {
    add_def: function() {
      //this.$store.commit('add');       actionsmutations
      this.$store.dispatch("addFunc");
    },
    add10: function() {
      //this.$store.commit('add');       actionsmutations
      //this.$store.commit('add', 10);
      this.$store.dispatch("addFunc", 10);
    },
    remove_def: function() {
      //this.$store.commit('remove');
      this.$store.dispatch("removeFunc");
    },
    remove10: function() {
      //this.$store.commit('remove');
      this.$store.dispatch("removeFunc", 10);
    },
    ...mapMutations([
      'add', // `this.add()`  `this.$store.commit('add')`
      'remove', // `this.remove()`  `this.$store.commit('remove')`
      'setNum1'
    ]),
    ...mapActions([
      'addFunc', // `this.addFunc()`  `this.$store.dispatch('addFunc')`
      'removeFunc', // `this.removeFunc()`  `this.$store.dispatch('removeFunc')`
      'setNumFunc2'
    ]),
    changedM1: function(n) {
      this.$store.commit('setNum1', n);      // actionsmutations
      //this.$store.m1.dispatch("setNum");
    },
    changedM2: function(n) {
      //this.$store.commit('add');       actionsmutations
      this.$store.dispatch("setNumFunc2", n);
    }
  },
  watch: {},
  computed: {
    // 
    ...mapState({ //
      count: state => state.count, //map  `this.count`  `this.$store.state.count`
      countFunc(state){ //
        return state.count + this.myCount;
      },
      num1: state => state.m1.num
    }),
    ...mapGetters([
      'getStoreStateCount', // `this.getStoreStateCount()`  `this.$store.getters.getStoreStateCount`
      'getTrueObjList' // `this.getStoreStateCount()`  `this.$store.getters.getTrueObjList`
    ])
  },
  mounted() {}
};
</script>


<style>
.vuex-main-div {
  width: 100%;
  margin-bottom: 50px;
}
.vuex-row-div {
  display: flex;
  line-height: 40px;
}
.vuex-left {
  width: 15%;
  font-weight: 600;
}
.vuex-left-div {
  width: 35%;
}

.vuex-right-div {
  width: 35%;
}

.vuex-right {
  width: 35%;
}
</style>




