import Vue from 'vue'
import Vuex from 'vuex'
import M1 from './modules/m1.js'
import M2 from './modules/m2.js'

// vuex
Vue.use(Vuex) 

// vuex
const store = new Vuex.Store({
	//namespacedtruemodulemodule
	namespaced: true,
	//vuex
	state:{ 
		count: 1,
		objList: [
			{id: 1, text: 'obj1', status: true},
			{id: 2, text: 'obj2', status: false},
			{id: 3, text: 'obj3', status: true}
		] 
	},
	//Gettervuecomputedgetter 
	//
	//vuexGetterGetters state
	getters:{
		getStoreStateCount: function(state){ //state
			return state.count;
		},
		getTrueObjList: function(state){
			return state.objList.filter(f=>f.status);
		}
	},
	//storemutation
	// mutation 
	// ... commit
	mutations: {
		add(state, n){
			if (!n) {
				n = 1;
			}
			state.count = state.count + n;
		},
		remove(state, n){
			if (!n) {
				n = 1;
			}
			state.count = state.count - n;
		},
	},
	//storeactions
	//actionsmutationactionsmutation
	//actionsmutations
	// ... dispatch
	actions: {
		addFunc(context, n){
			context.commit("add", n);
		},
		removeFunc(context, n){
			context.commit("remove", n);
		}
	},
	modules: {
		m1: M1,
		m2: M2
	}
})

export default store



