<template>
  <div>
    <p>child-attrs:{{$attrs}}</p><br>
    {{title}}
    <input v-model="title">
    <input v-model="message">
    <button v-on:click="childButton"></button>
    <br>child1
    <br>
    <child1 v-bind:title="title" v-bind="$attrs" v-on="$listeners"></child1>
    <br>child2
    <br>
    <child2></child2>
  </div>
</template>
<script>
import child1 from "./child1";
import child2 from "./child2";
export default {
  name: "child",
  render: function(createElement) {
    return createElement(
      "div",
      [
        createElement("div", {
          // DOM 
          domProps: {
            innerHTML: this.title
          }
        }),
        createElement("input", {
          style: {
            color: "red",
            fontSize: "14px"
          },
          //  HTML 
          attrs: {
            id: "foo"
          },
          // DOM 
          domProps: {
            value: this.message
          }
        })
      ],
      {
        props: ["message"],
        data() {
          return {
            title: "message="
          };
        },
        methods: {},
        mounted: {}
      }
    );
  },
  data() {
    return {
      title: "childmessage="
    };
  },
  //props 
  //props: ["message"],
  /* props: {
    message: String //
  }, */
  props: {
    message: {
      type: String,
      default: "defaultValue"
    }
  },
  components: {
    child1: child1,
    child2: child2
  },
  mounted: function() {
    //this.initData();
  },
  activated: function() {
    //keep-alive 
    console.log("child-activated");
  },
  deactivated: function() {
    //keep-alive 
    console.log("child-deactivated");
  },
  beforeDestroy: function() {
    console.log("child-beforeDestroy");
  },
  destroyed: function() {
    console.log("child-destroyed");
  },
  methods: {
    initData: function() {
      this.$parent.msg = "";
    },
    childButton: function() {
      this.$emit("childButton", this.message);
    }
  }
};
</script>


