<template>
  <div class="test8-main-div">
    <a href="https://cn.vuejs.org/v2/guide/" target="_blank"></a>
    <br>
    <br>
    <br>

    <button @click="change('base1')"></button>
    <button @click="change('base2')"></button>
    <button @click="change('base3')">Class  Style </button>
    <button @click="change('base4')"></button>
    <button @click="change('base5')"></button>
    <button @click="change('base6')"></button>

    <!--  -->
    <div class="test8-row-div" v-show="base1">
      <base-1></base-1>
    </div>

    <!--  -->
    <div class="test8-row-div" v-show="base2">
      <base-2></base-2>
    </div>

    <!-- Class  Style  -->
    <div class="test8-row-div" v-show="base3">
      <base-3></base-3>
    </div>

    <!--  -->
    <div class="test8-row-div" v-show="base4">
      <base-4></base-4>
    </div>

    <!--  -->
    <div class="test8-row-div" v-show="base5">
      <base-5></base-5>
    </div>

    <!--  -->
    <div class="test8-row-div" v-show="base6">
      <base-6></base-6>
    </div>
  </div>
</template>

<script>

import base1 from "./base1";
import base2 from "./base2";
import base3 from "./base3";
import base4 from "./base4";
import base5 from "./base5";
import base6 from "./base6";

export default {
  data() {
    return {
      base1: true,
      base2: false,
      base3: false,
      base4: false,
      base5: false,
      base6: false
    };
  },
  components: {
    base1,
    base2,
    base3,
    base4,
    base5,
    base6
  },
  methods: {
    change(data) {
      this.base1 = false;
      this.base2 = false;
      this.base3 = false;
      this.base4 = false;
      this.base5 = false;
      this.base6 = false;
      if (data === "base1") {
        this.base1 = true;
      } else if (data === "base2") {
        this.base2 = true;
      } else if (data === "base3") {
        this.base3 = true;
      } else if (data === "base4") {
        this.base4 = true;
      } else if (data === "base5") {
        this.base5 = true;
      } else if (data === "base6") {
        this.base6 = true;
      }
    }
  },
  filters: {},
  watch: {},
  computed: {}
};
</script>

<style>
.test8-main-div {
  width: 100%;
}

.test8-row-div {
  width: 100%;
  text-align: left;
}
.test8-main-div>button{
      outline: none;
    padding: 5px 8px;
    color: #fff;
    border: 1px solid #BCBCBC;
    border-radius: 3px;
    background-color: #009A61;
    cursor: pointer;
}
</style>





