<template>
  <div class="base1-main-div">
    <div class="base1-row-div">
      <h4>1.</h4>
      <p>
        Vue
        <br>+  =  = 
        <br>+ 
        <br>- 
        <br>+ beforeCreate data  methods 
        <br>+ createdOK data  methods OK 
        <br>+ beforeMount
        <br>+ mounted
        <br>- 
        <br>+ beforeUpdate  data  DOM
        <br>+ updated data   
        <br>- 
        <br>+ beforeDestroy
        <br>+ destroyedVue Vue 
      </p>

      <h3>beforeDestroy()</h3>
      <button @click="destoryVm">destoty vm</button>
      <p class="base1-row-div1-p" v-bind:class="{ 'base1-row-div1-p2': isShow }">()</p>
      <br>
      <h3></h3>
      <img src="..\img\lifecycle.png">
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isShow: true
    };
  },
  components: {},
  methods: {
    //vm
    destoryVm() {
      // 
      //  beforeDestroy  destroyed 
      //dom,
      this.$destroy();
    }
  },
  watch: {},
  computed: {},
  //
  //1.
  beforeCreate: function() {
    //el  data 
    console.log("vue:beforeCreate()");
  },
  created: function() {
    //data el 
    console.log("vue:created()");
  },
  beforeMount: function() {
    //el  data
    console.log("dom:beforeMount()");
  },
  mounted() {
    //(1)ajax...
    //ES6()=> var self = this
    this.instervalid = setInterval(() => {
      this.isShow = !this.isShow;
    }, 1000);
    //5s
    setTimeout(() => {
      this.$destroy();
    }, 5000);
    console.log("dom:mounted()");
  },
  //2.(N)
  beforeUpdate: function() {
    console.log(":beforeUpdate()");
  },
  updated: function() {
    console.log(":updated()");
  },
  //3.
  beforeDestroy() {
    //(1),...
    //
    console.log("vue:beforeDestroy()");
    clearInterval(this.instervalid);
  },
  destroyed: function() {
    console.log("vue:destroyed()");
  }
};
</script>


<style>
.base1-main-div {
  width: 100%;
}
.base1-row-div {
  width: 100%;
  text-align: left;
}
.base1-row-div img {
  width: 100%;
  text-align: left;
}
.base1-row-div1-p {
  height: 50px;
}
.base1-row-div1-p2 {
  color: red;
}
</style>




