<template>
  <div class="order-main-div">
    <!-- <a href="https://www.jianshu.com/p/c4a87e1b4ef7" target="_blank"></a> -->
    <div class="order-row-div">
      <div class="order-left"></div>
      <div class="order-left-div"></div>
      <div class="order-right-div"></div>
      <div class="order-right"></div>
    </div>

    <div class="order-row-div">
      <!-- v-texttextContentJStext -->
      <div class="order-left">v-text</div>
      <div class="order-left-div">
        <input type="text" v-model="vText" placeholder="">
      </div>
      <div class="order-right-div">     
        <span>{{vText}}</span>
      </div>
      <div class="order-right">[ {{vText}} ]</div>
    </div>

    <div class="order-row-div">
      <!-- HTMLHTMLv-htmlJSinnerHtml -->
      <div class="order-left">v-html</div>
      <div class="order-left-div">
        <input type="text" v-model="vHtml" placeholder="">
      </div>
      <div class="order-right-div">
        <div v-html="vHtml"></div>
      </div>
      <div class="order-right">[ {{vHtml}} ]</div>
    </div>

    <div class="order-row-div">
      <!-- v-preMustache -->
      <div class="order-left">v-pre</div>
      <div class="order-left-div">
        <input type="text" v-model="vPre" placeholder="">
      </div>
      <div class="order-right-div">
        <span v-pre>{{vPre}}</span> //
        <span>{{vPre}}</span>
      </div>
      <div class="order-right">[ {{vPre}} ]</div>
    </div>

    <div class="order-row-div" v-cloak>
      <!--  -->
      <div class="order-left">v-cloak</div>
      <div class="order-left-div">
        <input type="text" v-model="vCloak" placeholder="">
      </div>
      <div class="order-right-div">
        <span>{{vCloak}}</span>
      </div>
      <div class="order-right">[ {{vCloak}} ]</div>
    </div>

    <div class="order-row-div">
      <!-- v-once -->
      <div class="order-left">v-once</div>
      <div class="order-left-div">
        <input type="text" v-model="vOnce" placeholder="">
      </div>
      <div class="order-right-div">
        <span v-once>{{vOnce}}</span>
      </div>
      <div class="order-right">[ {{vOnce}} ]</div>
    </div>

    <div class="order-row-div">
      <!-- v-ifVue
      v-elsev-ifv-ifv-else-if
      v-else-ifv-ifelse-ifswitch-->
      <div class="order-left">v-if | v-else | v-else-if</div>
      <div class="order-left-div">
        <input type="radio" v-model="vif" value="1"> 1
        <input type="radio" v-model="vif" value="2"> 2
        <input type="radio" v-model="vif" value="3"> 3
        <input type="radio" v-model="vif" value="100"> 100
      </div>
      <div class="order-right-div">
        <div v-if="vif === '1'">A</div>
        <div v-else-if="vif === '2'">B</div>
        <div v-else-if="vif === '3'">C</div>
        <div v-else>other</div>
      </div>
      <div class="order-right">[ {{vif}} ]</div>
    </div>

    <div class="order-row-div">
      <!-- v-ifv-iffalsedom
      v-showdomcssdispaly-->
      <div class="order-left">v-show</div>
      <div class="order-left-div">
        <input type="radio" v-model="vShow" value="true"> true
        <input type="radio" v-model="vShow" value="false"> false
      </div>
      <div class="order-right-div">
        <div v-show="vShow === 'true'">vShowtrue</div>
      </div>
      <div class="order-right">[ {{vShow}} ]</div>
    </div>

    <div class="order-row-div">
      <div class="order-left">v-for </div>
    </div>
    <div class="order-row-div">
      <!-- v-bind :title="vBind + '123'" 
       JavaScript  v-bind:[attributeName]-->
      <div class="order-left">v-bind </div>
      <div class="order-left-div">
        <span>{{vBind}}</span>
      </div>
      <div class="order-right-div">
        <input type="button" :[attributeName]="vBind + '123456'">
      </div>
      <div class="order-right">[ {{vBind + '+' + ' "123456" '}} ]</div>
    </div>
    <div class="order-row-div">
      <div class="order-left">v-model </div>
      <div class="order-right-div">
        <span>v-model.lazy/.number/.trim</span>
      </div>
    </div>
    <div class="order-row-div">
      <div class="order-left">v-on </div>
      
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      vText: "<h3></h3>",
      vHtml: "<h3>HTML</h3>",
      vPre: "v-pre",
      vCloak: "",
      vOnce: "",
      vBind: "",
      attributeName: "value",
      vif: 1,
      vShow: "true"
    };
  },
  name: "order",
  components: {},
  methods: {},
  watch: {},
  computed: {},
  mounted() {}
};
</script>

<style>
.order-main-div {
  width: 100%;
}
.order-row-div {
  display: flex;
  line-height: 40px;
}
.order-left {
  width: 15%;
  font-weight: 600;
}
.order-left-div {
  width: 35%;
}

.order-right-div {
  width: 35%;
}

.order-right {
  width: 35%;
}
</style>




