<template>
  <div class="base5-main-div">
    <div class="base5-row-div">
      <h4>5.</h4>
      1.
      <a href="https://cn.vuejs.org/v2/guide/events.html" target="_blank"></a>
      <h4>
        .stop :
        <br>.prevent
        <br>.capture
        <br>.self
        <br>.once
        <br>.passive
      </h4>
      <a href="https://www.baidu.com/" @click.prevent="add"></a>
      <br>
      <br>2.
      <h3>.enter/ .tab / .delete () / .esc / .space / .up / .down / .left / .right</h3>
      <!--  .enter-->
      <p></p>
      <input type="text" v-model="text1" @keyup.enter="add">
      <button @click="add()"></button>
      <input type="text" v-model="text2">
      <br>
      <br>3.
      <a
        href="https://cn.vuejs.org/v2/guide/custom-directive.html"
        target="_blank"
      >()</a>
      <p></p>
      <p>v-color="'red'"</p>
      <span></span>
      <span v-color="'red'">{{ text1 }}</span>
    </div>
  </div>
</template>

<script>

/* 
//
Vue.config.keyCodes.f2 = 113;
 */

/* 
// v- 
//  `v-focus`
//bindinsertedjs


Vue.directive("focus", {
  //el:
  bind:function(el){
    //bind   
    //DOMfocus
    el.focus()     
    },
  inserted: function(el) {
    // DOM 
    el.focus();
  },
  updated:function(){
    //vnodeupdated
  }
}); 


Vue.directive("color", {
  bind:function(el,binding){
    //bind,
    //el.style.color = 'red' 
    console.log(binding.name) 
    console.log(binding.value) 
    //
    el.style.color = binding.value
    },
}); 
*/


export default {
  data() {
    return {
       text1: "111",
      text2: ""
    };
  },
  methods: {
    add() {
      console.log(this.text2);
      this.text2 = this.text1 + this.text2;
    }
  },
  //
  directives: {
    color: {
      bind: function(el, binding) {
        //binding.value
        el.style.color = binding.value;
      }
    }
  },
  watch: {},
  computed: {}
};
</script>


<style>
.base5-main-div {
  width: 100%;
}
.base5-row-div {
  width: 100%;
  text-align: left;
}

</style>




