<template>
  <div>
    <input  v-model="msg">
    :{{msg}}
    <button id="btnCreate" v-on:click="createButton"></button>
    <input v-model="qianmi">
    
    {{mi}}
    <br>:
    <input v-model="question">
    {{ answer }}
    <br>
    <button id="btnCreate" v-on:click="closeComp"></button>
    <br>
    <keep-alive include="child" exclude="test">
      <component
        ref="childView"
        :is="componentId"
        v-bind:question="question"
        v-bind:message.sync="msg"
        v-on:childButton="childClick"
        v-on:childButton1="childClick1"
      ></component>
    </keep-alive>
  </div>
</template>

<script>
import child from "./child";
import test from "./test";
export default {
  name: "func",
  data() {
    return {
      msg: "a",
      qianmi: 1,
      question: "",
      answer: "",
      componentId: "child",
      a: 2,
      childItem: { a: "111a" }
    };
  },
  computed: {
    //  getter
    mi: function() {
      // `this`  vm 
      return this.qianmi * 1000;
    }
  },
  watch: {
    //
    //  `question` 
    question: function(newQuestion, oldQuestion) {
      //  answer
      this.answer = "Waiting for you to stop typing...";
      //setTimeout(this.getAnswer(),5000);
      setTimeout(() => {
        this.getAnswer();
      }, 2000);
    }
  },
  components: {
    child: child,
    test: test
  },
  beforeCreate: function() {
    // (data observer)  event/watcher 
    //eldata 
    //loading
    console.log("beforeCreate");
    console.log(this.$el, "&el");
    console.log(this.$data, "&data");
    //this.startUp();startUp this.startUp is not a function
  },
  created: function() {
    //
    // (data observer)watch/event 
    //$el  Dom$elDOM
    // datael
    console.log("created");
    console.log(this.$el, "&el");
    console.log(this.$data, "&data");
    //loading
    this.startUp();
  },
  beforeMount: function() {
    console.log("beforeMount-");
    // el  data 
    console.log(this.$el, "&el");
    console.log(this.$data, "&data");
  },
  mounted: function() {
    //dom 
    //
    //
    console.log("mounted-");
    console.log(this.$el, "&el");
    console.log(this.$data, "&data");
  },
  beforeUpdate: function() {
    //
    console.log("beforeUpdate");
    console.log("==");
  },
  updated: function() {
    //
    //beforeUpdate updated
    // updated  vm.$nextTick  updated
    console.log("updated");
    console.log("====");
    this.$nextTick(function() {
      // Code that will run only after the
      // entire view has been re-rendered
      console.log("$nextTick.this.msg=" + this.msg);
    });
  },
  activated: function() {
    //keep-alive 
    //<keep-alive> Vue
    console.log("activated");
  },
  deactivated: function() {
    //keep-alive 
    //<keep-alive> Vue
    console.log("deactivated");
  },
  beforeDestroy: function() {
    //
    //
    //keep-alive (exclude) 
    console.log("beforeDestroy");
    console.log(this.$el, "&el");
    console.log(this.$data, "&data");
    console.log("beforeDestroy.msg=" + this.msg);
  },
  destroyed: function() {
    //Vue Vue 
    //
    //keep-alive (exclude) 
    console.log("destroyed");
    console.log(this.$el, "&el");
    console.log(this.$data, "&data");
    console.log("destroyed.msg=" + this.msg);
  },
  errorCaptured: function(error, vm, info) {
    //
    //
    // false 
    console.log("errorCaptured.error=" + error);
    console.log(vm, "vm");
    console.log("errorCaptured.info=" + info);
  },
  methods: {
    startUp: function() {
      console.log("startUp");
    },
    createButton: function() {
      this.$refs.childView.title = "qqqq";
      this.$refs.childView.initData();
    },
    getAnswer: function() {
      //  answer
      this.answer = "answer";
    },
    childClick: function(value) {
      this.msg = value;
    },
    childClick1: function(value) {
      this.question = value;
    },
    closeComp: function() {
      if (this.componentId === "test") {
        this.componentId = "child";
      } else {
        this.componentId = "test";
      }
    }
  }
};
</script>


