<template>
  <div class="base3-main-div">
    <div class="base3-row-div">
      <h4>3.Class  Style </h4>1. 
      <h4 :class="['red', 'thin']">H1</h4>2. 
      <h4 :class="['red',  isthin?'thin':'']"></h4>3. 
      <h4 :class="['red',  {'thin': isthin}]"></h4>4. 
      <h4 :class="{red:true, thin:true}">H1</h4>
      <h4 :class="classobj">H1</h4>5.style
      <h4 :style="{color: stylecolor, 'font-size': '10px'}">style</h4>
      <h4 :style="[styleObj, styleObj2]"></h4>

      <br>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isthin: false,
      classobj: {
        red: true,
        thin: true
      },
      stylecolor: "red",
      styleObj: { color: "red", "font-size": "10px", "font-weight": "200" },
      styleObj2: { fontStyle: "italic" }
    };
  },
  components: {},
  methods: {},
  watch: {},
  computed: {}
};
</script>


<style>
.base3-main-div {
  width: 100%;
}
.base3-row-div {
  width: 100%;
  text-align: left;
}
.red {
  color: red;
}
.thin {
  letter-spacing: 0.5em;
}
</style>




