<template>
  <div class="base4-main-div">
    <div class="base4-row-div">
      <h4>4.</h4>
      <p> v-bind </p>
      <p> </p>
      1.
      <p>{{msg1}}</p>
      <p>{{ msg | msgFormat('+1','123') | test}}</p>
      2.v-bind
      <p v-bind:title="msg | msgFormat('+1','123') "></p>
    </div>
  </div>
</template>

<script>

/* 
//
Vue.filter("msgFormat", function(msg, arg, arg2) {
  return msg.replace("", arg + arg2);
});
Vue.filter("test", function() {
  return msg + "======";
}); 
*/


export default {
  data() {
    return {
       msg: "",
        msg1: "{{ msg | msgFormat('+1','123') | test}}",
    };
  },
  //,
  filters: {
    msgFormat: function(msg, arg, arg2) {
      return msg.replace(//g, arg + arg2);
    },
    test: function(msg, arg, arg2) {
      return msg.replace(//g, ' ""');
    },
  },
  components: {},
  methods: {},
  watch: {},
  computed: {}
};
</script>


<style>
.base4-main-div {
  width: 100%;
}
.base4-row-div {
  width: 100%;
  text-align: left;
}
</style>




